import { ParseTypesEnum } from 'myst-common';
export const mdastDirective = {
    name: 'mdast',
    arg: {
        type: ParseTypesEnum.string,
        required: true,
    },
    run(data) {
        return [
            {
                type: 'mdast',
                id: data.arg,
            },
        ];
    },
};
