import { ParseTypesEnum } from 'myst-common';
export const includeDirective = {
    name: 'include',
    arg: {
        type: ParseTypesEnum.string,
        required: true,
    },
    run(data) {
        return [
            {
                type: 'include',
                file: data.arg,
            },
        ];
    },
};
