import { ParseTypesEnum } from 'myst-common';
export const mermaidDirective = {
    name: 'mermaid',
    body: {
        type: ParseTypesEnum.string,
        required: true,
    },
    run(data) {
        return [
            {
                type: 'mermaid',
                value: data.body,
            },
        ];
    },
};
