import { ParseTypesEnum } from 'myst-common';
export const iframeDirective = {
    name: 'iframe',
    arg: {
        type: ParseTypesEnum.string,
        required: true,
    },
    options: {
        label: {
            type: ParseTypesEnum.string,
        },
        width: {
            type: ParseTypesEnum.string,
            // length_or_percentage_or_unitless,
        },
        align: {
            type: ParseTypesEnum.string,
            // choice(["left", "center", "right"])
        },
    },
    run(data) {
        const { label, width, align } = data.options || {};
        return [
            {
                type: 'iframe',
                src: data.arg,
                label: label,
                width,
                align,
            },
        ];
    },
};
